  margin: 20px;
  padding: 0;
}

.shiny {
  background: #c1f7ff;
  border-radius: 28px;
  color: #34cbfd;
  border: 4px solid #f9ebeb3a;
  float: left;
  font-family: "Roboto", sans-serif;
  font-size: 48px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: 5px;
  margin-top: 5px;
  padding: 0;
  text-align: center;
  width: 100px;
  max-width: 100%;
  position:relative;
  overflow: hidden;
  z-index: 10;
  --offset: 3px;
  
}

.shiny::before{
  content: '';
  background: conic-gradient(transparent 0deg, #6591ef, #8dcdff,  #4a70f8,  #a075f2, transparent);
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  aspect-ratio: 1;
  width: 100%;
  animation: rotate 2s linear infinite;
  overflow: hidden;


}
.shiny::after{
  content: '';
  background: inherit;
  border-radius: inherit;
  position: absolute;
  inset: var(--offset);
  height: calc(100% - 2 * var(--offset));
  width: calc(100% - 2 * var(--offset));
}
.shiny text{
  background: transparent;
  color: #34cbfd;
  font-size: 48px;
  position: absolute;
  inset: 0;
  z-index: 10;
  padding: 1.78rem;

}

@keyframes rotate {
  from{
    transform: translate(-50%, -50%) scale(1.4) rotate(0turn);
  }

  to{
    transform: translate(-50%, -50%) scale(1.4) rotate(1turn);
  }
}

.square {
  background: #c1f7ff;
  border-radius: 28px;
  color: #34cbfd;
  border: 4px solid #6591ef98;
  float: left;
  font-family: "Roboto", sans-serif;
  font-size: 48px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: 5px;
  margin-top: 5px;
  padding: 0;
  text-align: center;
  width: 100px;
  transition: border .5s;
  overflow: hidden;
}

.square:hover {
  border: 2px solid #ffffff
}

.board-row {
  display: table;
  margin-left: auto; 
  margin-right: 40px;
  width: 340px
}

.board-row:after {
  position:absolute;
  clear: both;
  content: '';
  display: table;
}

.status {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  color: #6591ef98;  
  margin-left: auto; 
  margin-right: 200px;
  text-align: left;
}

.game-info {

  color: #6591ef98;
  margin-left: 20px; 
  margin-right: auto;
  width: 200px;
  
}

.gamebutton{
  background: #c1f7ff;
  border-radius: 28px;
  color: #34cbfd;
  border: 3px solid #6591ef98;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  font-weight: bold;
  line-height: 20px;
  height: 30px;

  padding: 0;
  text-align: center;
  width: 120px;
  margin-bottom: 4px;

}


.game {
  height: 100%;
  display: flex;
  flex-direction: row;
  margin: auto;
  width: 100%;
  margin-top: 6%;
  padding: 1px;
  
  

}

.centered{
  
  height: 100%;
  padding: 3px;
  width: 100%;
  margin-left: auto;
  
  
  
}

.log_header {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  color: #6591ef98;  
  margin-bottom: 10px;
  width: 340px;

  
  }
  
@media only screen and (max-width: 700px) {
  .game {
    flex-direction: column;
    width: 100%;
    
  }
  .centered{
    margin: auto;

  }
  .board-row {
    margin:auto;
  }
  .game-info {
    width: 340px;
    margin:auto;
    
  }
  .log_header{
    margin:auto;
    margin-bottom: 10px;

  }
}
